import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
Vue.use(Vuex)
const state = {
  hasLogin: false,
  userInfo: '',
  language: true,
  countries: [
    {
      value: '1',
      label: this.language ? '':'Asia',
      children: ''
    },
    {
      value: '2',
      label: this.language ? '':'Europe',
      children: ''
    },
    {
      value: '3',
      label: this.language ? '':'Africa',
      children: ''
    },
    {
      value: '4',
      label: this.language ? '':'North America',
      children: ''
    },
    {
      value: '5',
      label: this.language ? '':'South America',
      children: ''
    },
    {
      value: '6',
      label: this.language ? '':'Oceania',
      children: ''
    },
    {
      value: '7',
      label: this.language ? '':'Antarctica',
      children: ''
    },
   ],
}
export default new Vuex.Store({
  state,
  mutations,
  actions
})



