         <div>
           <div>
             <el-select v-model="country_id" placeholder="" style="width: 460px;height: 30px;">
               <el-option
                 v-for="item in options1"
                 :key="item.value"
                 :label="item.label"
                 :value="item.value">
               </el-option>
             </el-select>
           </div>
           <p class="remark">()</p>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span></h2>
         <div>
           <el-select v-model="money_type" placeholder="CNY" style="width: 460px;height: 30px;">
             <el-option
               v-for="item in options1"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </div>
       </div>
       <div class="item">
