           <div style="display: flex;align-items: flex-end">
             <p style="border-bottom:1px solid #314E8C;width: 150px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
               <input type="number"
                      v-model="premium"
                      style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
               <span>%</span>
             </p>
             <div>
               <p style="width: 30px;height: 18px;line-height: 18px;text-align: center;background-color:#314E8C "><i class="el-icon-caret-top" style="color: #fff" @click="add"></i></p>
               <p style="width: 30px;height: 18px;line-height: 18px;text-align: center;background-color:#314E8C;margin-top: 3px; "><i class="el-icon-caret-bottom" style="color: #fff" @click="reduce"></i></p>
             </div>
           </div>
           <p class="remark"></p>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span></h2>
         <div>
           <p style="color: #E0180F;font-size:22px;">27862.00<span style="margin-left: 4px;font-size:12px;color: #314E8C">CNY/BTC</span></p>
           <p class="remark">(*)</p>
         </div>
       </div>
       <div class="item" v-if="radio == 3 || radio == 4">
         <h2><span>*</span></h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 460px;text-align: left;font-size:24px;color:#314E8C;display: flex">
             <input type="text" style="width:460px;text-align: left;font-size:24px;color: #314E8C;border: none;" maxlength="18">
           </p>
           <p class="remark">()</p>
         </div>
       </div>
       <div class="item" v-if="radio == 2">
         <h2><span>*</span></h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 150px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number"
                    style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;"
                    maxlength="6"
                    @change="chackMinutes"
                    v-model="minutes">
             <span></span>
           </p>
           <p class="remark">(, )</p>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span></h2>
         <div>
           <el-select v-model="payment_channel_ids" placeholder="" style="width: 460px;height: 30px;">
             <el-option
               v-for="item in options1"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span></h2>
         <div>
           <el-input
             style="width: 460px;height: 140px;"
             type="textarea"
             :rows="6"
             placeholder=""
             v-model="description">
           </el-input>
           <p class="remark">()</p>
         </div>
       </div>
       <!--<div class="item">-->
         <!--<h2><span style="margin-right: 18px"></span></h2>-->
         <!--<div>-->
           <!--<el-switch-->
             <!--v-model="value2"-->
