         <!--</div>-->
       <!--</div>-->
       <!--<div class="item">-->
         <!--<h2><span style="margin-right: 18px"></span></h2>-->
         <!--<div>-->
           <!--<el-switch-->
             <!--v-model="value2"-->
             <!--on-text=""-->
             <!--off-text="">-->
           <!--</el-switch>-->
           <!--<p class="remark">()</p>-->
         <!--</div>-->
       <!--</div>-->
       <div class="item">
         <h2><span style="margin-right: 18px"></span></h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 140px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number" style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
             CNY
           </p>
           <p class="remark">()</p>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span></h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 140px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number" style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
             CNY
           </p>
           <p class="remark">( Eagle wallet )</p>
         </div>
       </div>
       <div class="flex-center" style="width: 100%;">
         <div class="btn flex-center" @click="createProducts()">
           <p></p>
         </div>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import { mapState } from 'vuex'
import {httpGet,httpPost} from '../../utils/index'

export default {
  data () {
    return {
      radio: '2',
      options1: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      payment_channel_ids: '',
      value2: true,
      input: '',
      description: '',
      premium:1,
      minutes:2,
      money_type:'',
      country_id:'',
      min_trade_amount:'',
      max_trade_amount:'',
      address:''
    }
  },
  computed: {
    ...mapState([''])
  },
  methods: {
    createProducts () {
      const that = this;
      httpPost('/products/create',{
        type: that.radio,
        country_id: that.country_id,
        symbol_type: '',
        money_type: '',
        price: '',
        premium: '',
        payment_channel_ids: '',
        minutes: '',
        description: '',
        min_trade_amount:'',
        max_trade_amount:'',
        address:'',
      },function (res) {
        if(res.data.error_code === 0){
          console.log('')
        }
      })
    },
    add(){
      this.premium++;
    },
    reduce(){
      this.premium--;
      if(this.premium < 0){
        this.premium = 0;
      }
    },
    chackMinutes(){
      if(this.minutes < 0){
        this.minutes = 0;
      }
    }
  }
}
</script>

<style scoped>
  .header{
    padding: 0 90px;
