    letter-spacing: -0.2px;
    margin-top: 10px;
  }
  .btn{
    width: 400px;
    height: 60px;
    margin: 0 auto;
    background-color: #314E8C;
    margin: 136px 0 84px 0;
  }
  .btn p{
    font-size:24px;
    color: #fff;
  }
  .list-success{
    width: 100%;
    height: 780px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .list-success img{
    width: 116px;
    height: 128px;
    margin-top: 120px;
  }
  .list-success p{
    ont-family: PingFangSC-Regular;
    font-size: 24px;
    color: #333333;
    letter-spacing: -0.41px;
    margin-top: 45px;
  }
</style>



